import Immutable from 'seamless-immutable';
import axios from 'axios';

// name spacing with prefix `gatolocho/`
const GATOLOCHO_FAILURE = 'gatolocho/GATOLOCHO_FAILURE';
const LOAD_PESAN = 'gatolocho/LOAD_PESAN';
const LOAD_PESAN_SUCCESS = 'gatolocho/LOAD_PESAN_SUCCESS';

// Create Immutable object
const actionTypes = Immutable({
  GATOLOCHO_FAILURE,
  LOAD_PESAN,
  LOAD_PESAN_SUCCESS,
});

const BASE_URL = 'https://firestore.googleapis.com/v1beta1/projects/adnanfajlur1999/databases/(default)/documents';

// export it
export default actionTypes;

/* Action Creator */
export function gatolochoFailure(error) {
  return {
    type: actionTypes.GATOLOCHO_FAILURE,
    error,
  };
}

// Mygram
export function loadPesan() {
  return dispatch => {
    axios.get(`${BASE_URL}/message`)
      .then(res => {
        dispatch(loadPesanSuccess(res));
      })
      .catch(err => {
        dispatch(gatolochoFailure(err));
        return Promise.reject(err);
      });
  };
}

export function loadPesanSuccess(data) {
  return {
    type: actionTypes.LOAD_PESAN_SUCCESS,
    data,
  };
}



