import Immutable from 'seamless-immutable';
import actionTypes from '../actions/gatolochoAction';

export const INITIAL_STATE = Immutable({
  loading: false,
  error: null,
  satu: null,
});

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case actionTypes.GATOLOCHO_FAILURE:
      return state.merge({ error: action.data, loading: false });

    case actionTypes.LOAD_PESAN:
      return state.merge({ satu: null, loading: true });

    case actionTypes.LOAD_PESAN_SUCCESS:
      return state.merge({ satu: action.data, loading: false, error: false });

    default:
      return state;
  }
}

export default reducer;



